import { TaskUploadService } from './task-upload.service';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { FuseSharedModule } from '@fuse/shared.module';
import { MatButtonModule, MatFormFieldModule, MatIconModule, MatInputModule, MatTableModule} from '@angular/material';
import { BulkTaskUploadComponent } from './bulk-task-upload/bulk-task-upload.component';
import { FileUploadModule } from 'ng2-file-upload';

const routes: Routes = [
  {
      path     : '',
      component: BulkTaskUploadComponent
  }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    FuseSharedModule,
    MatButtonModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatTableModule,
    FileUploadModule
  ],
  providers: [TaskUploadService],
  declarations: [BulkTaskUploadComponent]
})
export class TaskUploadModule { }
