import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { Component, OnInit, EventEmitter } from '@angular/core';
import { FileUploader, FileItem } from 'ng2-file-upload';
import { TaskUploadService } from '../task-upload.service';
import { ToasterService } from '@fuse/services/toaster-service.service';

export interface PeriodicElement {
  name: string;
  position: number;
  date: string;
}

const ELEMENT_DATA: PeriodicElement[] = [
  {position: 1, name: 'File00012334444545436565465476', date: '12/02/2019'},
  {position: 2, name: 'File00012334444546767567656576575646', date: '12/02/2019'},
  {position: 3, name: 'File000123356756767657654444546', date: '12/02/2019'}
];

@Component({
  selector: 'app-bulk-task-upload',
  templateUrl: './bulk-task-upload.component.html',
  styleUrls: ['./bulk-task-upload.component.scss']
})
export class BulkTaskUploadComponent implements OnInit {
  public uploader: FileUploader;
  displayedColumns: string[] = ['position', 'name', 'date'];
  dataSource = ELEMENT_DATA;
  selectedFile: File;
  fileName = 'Select';

  constructor(private taskupload: TaskUploadService,
              private apiloader: ApiLoaderService,
              private toast: ToasterService) {

            this.uploader  = new FileUploader({
                url: 'http://3.17.93.116/align/public/api/tasks/import',
                disableMultipart: false,
                autoUpload: false,
                method: 'post'
            });
  }

  ngOnInit() {
        this.uploader.onErrorItem = (item, response) => this.onErrorItem(item, response);
        this.uploader.onSuccessItem = (item, response) => this.onSuccessItem(item, response);
  }

  uploadFile(){
      this.uploader.uploadAll();
  }

  public onFileSelected(event: EventEmitter<File[]>) {
    this.selectedFile = event[0];
    this.fileName = this.selectedFile.name;
  }

  onSuccessItem(item: FileItem, response: string): any {
      const data = JSON.parse(response); //success server response
      console.log(data);
  }

  onErrorItem(item: FileItem, response: string): any {
      console.log(response);
      /* let error = JSON.parse(response); //error server response
      console.log(error); */
  }

}
